import React from 'react';
import './style.css';
import Navbar from '../../components/Navbar/Navbar';
import Footer from '../../components/Footer/footer';
import Card from '../../components/Student Result/studentCard';
import {foundationCommerce, intermediate} from './Data';

const StudentResult = () => {
    return(
        <div>
            <Navbar/>
                        <div className="student_result_section"> 
                        <div className="utility_center_text">
                               <h1 className="batch_title" >CA FOUNDATION</h1>
                               <h1 className="result_percentage" >72.41% result</h1>
                               <div className="student_card--container">
                                 {
                                     foundationCommerce.map(student => {
                                         return (
                                            <Card 
                                               number={student.key}
                                               img={student.image}
                                               score={student.score}
                                               name={student.name} 
                                                 />
                                         )
                                     })
                                 }
                                </div> 
                                <h1 className="batch_title utility_margin-1" >CA INTERMEDIATE</h1>
                                <h1 className="result_percentage" >100% result</h1>
                               <div className="student_card--container">
                                 {
                                     intermediate.map(student => {
                                         return (
                                            <Card 
                                               number={student.key}
                                               img={student.image}
                                               score={student.score}
                                               name={student.name} 
                                                 />
                                         )
                                     })
                                 }
                                </div>  
                               </div> 
                        </div>
            <Footer/>
        </div>
    )
}
export default StudentResult;